
.fixed_nav[data-v-be3a18c8]{
	border: 1px solid #d8d8d8;
}
.fixed_nav p[data-v-be3a18c8]{
	width:100%;
	height: 40px;
	text-align: center;
}
.fixed_nav [data-v-be3a18c8]:nth-child(2){
	border-top: 1px solid #d8d8d8;
	/*border-bottom: 1px solid #d8d8d8;*/
 -o-box-sizing: border-box;
 -ms-box-sizing: border-box;
 box-sizing: border-box;
}
.fixed_nav [data-v-be3a18c8]:nth-child(3){
	border-top: 1px solid #d8d8d8;
 -o-box-sizing: border-box;
 -ms-box-sizing: border-box;
 box-sizing: border-box;
}
.fixed_nav p[data-v-be3a18c8]{
	position: relative;
	cursor: pointer;
	background: #fff;
}
.fixed_nav p i[data-v-be3a18c8]{
	width: 22px;
	height: 22px;
	font-size: 18px;
	position: absolute;
	top:0;
	right: 0;
	left: 0;
	bottom: 0;
	margin: auto;
}
#fixed_nav_p1:hover i[data-v-be3a18c8]{
	color: #009e4d;
}
#fixed_nav_p1 i[data-v-be3a18c8]{
	color: #999;
	/*display: none;*/
}
#fixed_nav_p2 i[data-v-be3a18c8]{
	color: #999;
	border: 0;
}
#fixed_nav_p2 a[data-v-be3a18c8]{
	display: block;
	height: 100%;
}
#fixed_nav_p2:hover i[data-v-be3a18c8]{
	color: #009e4d;
}
#fixed_nav_p1:hover span[data-v-be3a18c8],#fixed_nav_p2:hover span[data-v-be3a18c8],#fixed_nav_p3:hover span[data-v-be3a18c8]{
	animation:mymove .5s 1 linear;
	-webkit-animation:mymove .5s 1 linear;
	-moz-animation:mymove .5s 1 linear;
	-ms-animation:mymove .5s 1 linear;

	animation-fill-mode:forwards;
	-webkit-animation-fill-mode:forwards;
	-moz-animation-fill-mode:forwards;
	-ms-animation-fill-mode:forwards;
}
@keyframes mymove{
from{opacity: .5;left: -110px;
}
to{opacity: 1;left: -90px;
}
}
@-webkit-keyframes mymove{
from{opacity: .5;left: -110px;
}
to{opacity: 1;left: -90px;
}
}
#fixed_nav_p1 span[data-v-be3a18c8],#fixed_nav_p2 span[data-v-be3a18c8],#fixed_nav_p3 span[data-v-be3a18c8]{
	opacity: 0;
	position: absolute;
	color: #999;
	top: 1px;
	text-align: center;
	left: -90px;
	border: 1px solid #d8d8d8;
	padding: 0 10px;
	height: 36px;
	line-height: 36px;
	font-size: 12px;
	z-index: 999;
	border-radius: 4px;
	background: #fff;
}
#fixed_nav_p1 span[data-v-be3a18c8]:after,#fixed_nav_p2 span[data-v-be3a18c8]:after,#fixed_nav_p3 span[data-v-be3a18c8]:after{
	content: '';
	width: 10px;
	height: 10px;
	position: absolute;
	right: -7px;
	top: 11px;
	background: #FFF;
	border-top: 1px solid #d8d8d8;
	border-right: 1px solid #d8d8d8;
	transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
}
#fixed_nav_p3 i[data-v-be3a18c8]{
	display: block;
	width: 100%;
	height: 100%;
}
#fixed_nav_p3 i img[data-v-be3a18c8]{
	width: 100%;
	height:100%;
}
#fixed_nav_p3 i[data-v-be3a18c8]{
	background: #999;
}
#fixed_nav_p3:hover i[data-v-be3a18c8]{
	background: #009e4d;
}




