  width: auto;
  height: 100%;
}
.index-logo-wrap[data-v-db6bb7cc]{width:100%;height:158px;border-bottom:2px solid #009e4d;
}
.logo-up[data-v-db6bb7cc]{padding-top:40px;margin:0 auto;
}
