  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}
.el-carousel__item[data-v-db6bb7cc]:nth-child(2n) {
   background-color: #99a9bf;
}
.el-carousel__item[data-v-db6bb7cc]:nth-child(2n+1) {
   background-color: #d3dce6;
}
.index-center[data-v-db6bb7cc]{width:1200px;margin:0 auto 50px;min-height:400px;height:auto!important;height:400px;
}
.index-center[data-v-db6bb7cc]:after{content:".";display:block;height:0;clear:both;overflow:hidden;visibility:hidden;
}
.index-center-list[data-v-db6bb7cc]{
  width:100%;
  height:auto;
  padding:55px 0 10px 0;
}
.index-center-list header[data-v-db6bb7cc]{
  padding:6px 0;
  height:22px;
  line-height:22px;
  overflow:hidden;
  border-bottom:3px solid #777;
}
.index-center-list header span[data-v-db6bb7cc]{
  float: left;
  font-size: 20px;
  height: 20px;
  line-height: 20px;
  font-weight: 900;
  padding-left: 10px;
  border-left: 3px solid #009e4d;
}
.index-center-list header i[data-v-db6bb7cc]{
  float:right;
  cursor:pointer;
  font-size: 14px;
}
.list-content[data-v-db6bb7cc]{
  padding-top:5px;
  overflow:hidden;
  text-align: center;
}
.list-content [data-v-db6bb7cc]:nth-child(5n) {
  margin-right:0;
}
.list-content [data-v-db6bb7cc]:nth-child(5n+1) {
  margin-left:0;
}
.list-content [data-v-db6bb7cc]:nth-child(1) {
  margin-left:0;
}
.list-content a[data-v-db6bb7cc]{
  float:left;
  width:224px;
  margin:24px 10px 0 10px;
  height: auto;
  overflow: hidden;
}
.list-content dl[data-v-db6bb7cc]{
  border:1px solid #ededed;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
}
.list-content a:hover dl[data-v-db6bb7cc]{
  border:1px solid #009e4d;
  box-shadow: 0 0 50px 1px #d1cece;
}
.list-content a dl img[data-v-db6bb7cc]{
  -webkit-transition: all .3s linear;
  transition: all .3s linear;
}
.list-content a:hover dl img[data-v-db6bb7cc]{
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}
.list-content dl [data-v-db6bb7cc]:nth-child(1){
  overflow: hidden;
  height:222px;
}
.list-content dl :nth-child(1) img[data-v-db6bb7cc]{
  width:100%;
  height:100%;
}
.list-content dl [data-v-db6bb7cc]:nth-child(2) {
  font-size: 14px;
  line-height:34px;
  height:34px;
  padding:0 15px;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}
.list-content dl [data-v-db6bb7cc]:nth-child(3){
  line-height: 34px;
  color:#009e4d;
}
.list-content dl :nth-child(3) i[data-v-db6bb7cc]{
  color:#009e4d;
  font-size:16px;
  line-height:30px;
}
.list-content dl :nth-child(3) span[data-v-db6bb7cc]{
  color:#009e4d;
  font-size:22px;
  padding: 0;
}



