
.header-cart-num{
	width: 20px;
	height: 20px;
	border-radius: 10px;
	-moz-border-radius: 10px;
	-webkit-border-radius: 10px;
	background: #009e4d;
	position: absolute;
	text-align: center;
	line-height: 20px;
	top: 0;
	right: -6px;
	color: #fff;
	font-size: 14px;
}
.t-index-header{width: 100%;height: 100%
}
.t-index-header .header-content{
	width: 1200px;
	margin: 0 auto;
	height: 110px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
.t-index-header .header-content div:nth-child(2){
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
.t-index-header .search-area{
	width: 400px;
	height: 40px;
	line-height: 43px;
	border-bottom: 1px solid #D1D1D1;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	color: #999;
}
.t-index-header .search-area input{
	width: 100%;
	height: 100%;
}
.t-index-header .icon-llhomesearch{
	width: 10%;
	text-align: center;
	font-size: 20px;
	color: #999;
}
.t-index-header .cart-area{
	width: 60px;
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: end;
	    -ms-flex-pack: end;
	        justify-content: flex-end;
	padding-top: 6px;
}
.t-index-header .cart-area span{
	font-size: 26px;
	color: #999;
}




